<template>
<div class="result-container">
    <div class="title-wrap">
        <!-- 顶部的标题 -->
        <h2 class="title">{{ this.$route.query.q }}</h2>
        <span class="sub-title">找到{{ total }}个结果</span>
    </div>
    <el-tabs v-model="activeIndex">
        <el-tab-pane label="歌曲" name="songs">
            <table class="el-table">
                <thead>
                    <th></th>
                    <th>音乐标题</th>
                    <th>歌手</th>
                    <th>专辑</th>
                    <th>时长</th>
                </thead>
                <tbody>
                    <tr class="el-table__row" v-for="(item,index) in songList" :key="index" @dblclick="playMusic(item.id)">
                        <td>{{ index+1 }}</td>
                        <td>
                            <div class="song-wrap">
                                <div class="name-wrap">
                                    <span>{{ item.name}}</span>
                                    <!-- mv图标 -->
                                    <span class="iconfont icon-mv" v-if="item.mvid!=0" @click="toMv(item.mvid)"></span>
                                </div>
                                <!--  二级标题 -->
                                <span v-if="item.alias.length!=0">{{ item.alias[0] }}</span>
                            </div>
                        </td>
                        <td>{{ item.artists[0].name }}</td>
                        <td>{{ item.album.name }}</td>
                        <td>{{ item.druation }}</td>
                    </tr>
                </tbody>
            </table>
        </el-tab-pane>
        <el-tab-pane label="歌单" name="lists">
            <div class="items">
                <div class="item" v-for="(item,index) in playlists" :key="index" @click="toplaylist(item.id)">
                    <div class="img-wrap">
                        <div class="num-wrap">
                            播放量:
                            <span class="num">{{ item.playCount }}</span>
                        </div>
                        <img :src="item.coverImgUrl" alt />
                        <span class="iconfont icon-play"></span>
                    </div>
                    <p class="name">{{ item.name }}</p>
                </div>
            </div>
        </el-tab-pane>
        <el-tab-pane label="MV" name="mv">
            <div class="items mv">
                <div class="item" v-for="(item,index) in mvList" :key="index" @click="toMv(item.id)">
                    <div class="img-wrap">
                        <img :src="item.cover" alt />
                        <span class="iconfont icon-play"></span>
                        <div class="num-wrap">
                            <div class="iconfont icon-play"></div>
                            <div class="num">{{ item.playCount }}</div>
                        </div>
                        <span class="time">{{ item.druation }}</span>
                    </div>
                    <div class="info-wrap">
                        <div class="name">{{ item.name }}</div>
                        <div class="singer">{{ item.artistName }}</div>
                    </div>
                </div>
            </div>
        </el-tab-pane>
    </el-tabs>
    <el-pagination @current-change="handleCurrentChange" background layout="prev, pager, next" :total="total" :current-page="page" :page-size="10" :limit="limit">
    </el-pagination>
</div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'result',
    data() {
        return {
            total: 0,
            activeIndex: 'songs',
            songList: [],
            playlists: [],
            mvList: [],
            count: 0,
            type: 1,
            limit: 10,
            page: 1
        };
    },
    created() {
        this.songSearch()
        // console.log(this.$route.query.q)
    },
    watch: {
        activeIndex() {
            let type = 1
            let limit = 10
            this.page = 1
            switch (this.activeIndex) {
                case 'songs':
                    this.type = 1
                    break;
                case 'lists':
                    this.type = 1000
                    console.log(this.type)
                    break;
                case 'mv':
                    this.type = 1004
                    break;
                default:
                    break;
            }
            axios({
                url: " https://autumnfish.cn/search",
                method: "get",
                params: {
                    keywords: this.$route.query.q,
                    type: this.type,
                    limit: this.limit
                }
            }).then(res => {
                // console.log(res)
                if (this.type == 1) {
                    this.songList = res.data.result.songs
                    this.songCount = res.data.result.songCount
                    this.total = res.data.result.songCount
                    for (let i = 0; i < this.songList.length; i++) {
                        let min = parseInt(this.songList[i].duration / 1000 / 60)
                        let sec = parseInt(this.songList[i].duration / 1000 % 60)
                        if (min < 10) {
                            min = "0" + min
                        }
                        if (sec < 10) {
                            sec = "0" + sec
                        }
                        // console.log(min + "|" + sec)
                        this.songList[i].druation = min + ":" + sec
                    }
                } else if (this.type == 1000) {
                    this.playlists = res.data.result.playlists
                    this.total = res.data.result.playlistCount
                    console.log(res)
                    for (let i = 0; i < this.playlists.length; i++) {
                        if (this.playlists[i].playCount > 100000) {
                            this.playlists[i].playCount = parseInt(this.playlists[i].playCount / 10000) + "万"
                        }
                    }
                } else {
                    this.mvList = res.data.result.mvs
                    this.total = res.data.result.mvCount
                    console.log(res)
                    for (let i = 0; i < this.mvList.length; i++) {
                        let min = parseInt(this.mvList[i].duration / 1000 / 60)
                        let sec = parseInt(this.mvList[i].duration / 1000 % 60)
                        if (min < 10) {
                            min = "0" + min
                        }
                        if (sec < 10) {
                            sec = "0" + sec
                        }
                        // console.log(min + "|" + sec)
                        this.mvList[i].duration = min + ":" + sec
                        if (this.mvList[i].playCount > 100000) {
                            this.mvList[i].playCount = parseInt(this.mvList[i].playCount / 10000) + '万'
                        }
                    }
                }
                // console.log(res)

            })
        }

    },
    methods: {
        playMusic(id) {
            axios({
                url: 'https://autumnfish.cn/song/url',
                method: "get",
                params: {
                    id
                }
            }).then(res => {
                // console.log(res)
                let url = res.data.data[0].url
                // console.log(this.$parent.musicUrl)
                this.$parent.musicUrl = url
            })
        },
        songSearch() {
            axios({
                url: " https://autumnfish.cn/search",
                method: "get",
                params: {
                    keywords: this.$route.query.q,
                    type: 1,
                    limit: this.limit,
                    offset: (this.page - 1) * this.limit,
                }
            }).then(res => {
                console.log(res)
                this.songList = res.data.result.songs
                this.songCount = res.data.result.songCount
                this.total = res.data.result.songCount
                for (let i = 0; i < this.songList.length; i++) {
                    let min = parseInt(this.songList[i].duration / 1000 / 60)
                    let sec = parseInt(this.songList[i].duration / 1000 % 60)
                    if (min < 10) {
                        min = "0" + min
                    }
                    if (sec < 10) {
                        sec = "0" + sec
                    }
                    // console.log(min + "|" + sec)
                    this.songList[i].druation = min + ":" + sec
                }
            })
        },
        toMv(id) {
            this.$router.push(`/mv?q=${id}`)
            // this.$router.go(0)
        },
        //歌单详情页
        toplaylist(id) {
            this.$router.push(`/playlist?q=${id}`)
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.page = val;
            axios({
                url: ' https://autumnfish.cn/search',
                method: 'get',
                params: {
                    keywords: this.$route.query.q,
                    limit: this.limit,
                    offset: (this.page - 1) * this.limit,
                    type: this.type
                }
            }).then(res => {
                // console.log(res);
                if (this.type == 1) {
                    //单曲
                    this.songList = res.data.result.songs;
                    this.count = res.data.result.songCount
                    this.total = res.data.result.songCount
                    for (let i = 0; i < this.songList.length; i++) {
                        let duration = this.songList[i].duration;
                        let min = parseInt(duration / 1000 / 60);
                        if (min < 10) {
                            min = '0' + min
                        }
                        let sec = parseInt(duration / 1000 % 60)
                        if (sec < 10) {
                            sec = '0' + sec
                        }
                        //console.log(min+" "+sec)
                        this.songList[i].duration = `${min}:${sec}`
                    }
                } else if (this.type == 1000) {
                    //歌单
                    console.log(res)
                    this.playlists = res.data.result.playlists;
                    this.total = res.data.result.playlistCount
                    for (let i = 0; i < this.playList.length; i++) {
                        if (this.playlists[i].playCount > 100000) {
                            this.playlists[i].playCount = parseInt(this.playlists[i].playCount / 10000) + '万'
                        }
                    }
                } else {
                    //mv
                    this.mvList = res.data.result.mvs;
                    this.total = res.data.result.mvCount
                    for (let i = 0; i < this.mv.length; i++) {
                        if (this.mvList[i].playCount > 100000) {
                            this.mvList[i].playCount = parseInt(this.mvList[i].playCount / 10000) + '万'
                        }
                    }
                }

            })
        }
    }

};
</script>

